var PanelView = React.createClass({displayName: "PanelView",
  toggleConnect(index) {
    let item = this.props.items[index];

    item.connected = !item.connected;

    this.state.itemsStatus[index] = item.connected;

    if(item.connected) {
      item.gain.connect(audioContext.destination);
      item.gain.connect(analyser);
    } else {
      item.gain.disconnect(audioContext.destination);
      item.gain.disconnect(analyser);
    }

    this.setState({
      itemsStatus: this.state.itemsStatus
    });
  },
  showProps(index) {
    let item = this.props.items[index];
    properties.setProps({item: item.properties});
  },
  names() {
    return this.props.items.map((item, index) => {
      return React.createElement("li", {key: index, 
                 onClick: this.showProps.bind(this, index)}, 
        React.createElement("span", {className: this.state.itemsStatus[index] ? 'fa fa-dot-circle-o' : 'fa fa-circle-o', 
              onClick: this.toggleConnect.bind(this, index)}), 
        item.name
      )
    });
  },
  timelapses() {
    let els = this.props.items.map((item, index) => {
      return React.createElement("canvas", {id: 'i-'+index});
    });

    els.forEach(el => {
      let c = el.getContext('2d');

      
    });
  },
  render() {
    return React.createElement("div", null, 
      React.createElement("ul", {className: "names"}, 
        this.names()
      ), 
      React.createElement("ul", {className: "timelapse"}, 
        this.timelapses()
      )
    );
  }
});

var PanelFactory = React.createFactory(PanelView);
var PropertiesView = React.createClass({displayName: "PropertiesView",
  updateProp(prop) {
    let el = this.refs[prop].getDOMNode();
    let item = this.props.item;
    item[prop] = parseInt(el.value, 10) || el.value;

    this.setProps({
      item: item
    })
  },
  input(prop) {
    let value = this.props.item[prop];
    let type = this.props.item.types[prop];
    if(type.type === 'select') {
      return React.createElement("select", {value: value, ref: prop, onChange: this.updateProp.bind(this, prop)}, 
        type.options.map(opt => {
          return React.createElement("option", {value: opt}, opt)
        })
      );
    } else {
      return React.createElement("input", {value: value, ref: prop, type: type.type, onChange: this.updateProp.bind(this, prop)})
    }
  },
  propertyList() {
    let els = [];
    for(let prop in this.props.item) {
      if(prop === 'types') continue;
      els.push(
        React.createElement("li", {key: prop}, 
          React.createElement("span", null, prop), 
          this.input(prop)
        )
      );
    }
    return els;
  },
  render() {
    if(!this.props.item) return React.createElement("span", null);
    return React.createElement("ul", {className: "properties"}, 
      React.createElement("p", null, "Properties"), 
      this.propertyList()
    )
  }
});

var PropertiesFactory = React.createFactory(PropertiesView);